.layout--homepage {
    .heros {
      max-width: map-get($layout, content-max-width) * 2;
      margin-right: auto;
      margin-left: auto;
    }
    .hero {
      img {
        display: block;
        width: 100%;
        margin: 0 auto;
      }
    }
    .hero__content {
      margin-bottom: 0;
    }
    .hero__cover {
      max-width: map-get($layout, content-max-width);
    }
    .hero__cover--full-width {
      max-width: none;
    }
    .hero__title {

        strong{
            position: relative;
        }

        strong::before, strong::after {
            content: " ";
            position: absolute;
            top: 50%;
            height: 2px;
            width: 40%;
            margin: 0 10px;
            display: inline-block;
            background: #333;
        }
        strong:before{
            right: 100%;
        }
        strong:after{
            left: 100%;
        }

        margin-top: auto;
        font-family: 'Merienda', cursive;
    }

    .article-list {
        .item{
            border-bottom-width: 0;
        }

        .item__content {
            .item__head {
                .item__title {
                    margin: 0.1rem;
                    font-size: 1.4rem;
                    font-family: 'Baloo Tamma 2', cursive;
                    color: #3377CC;

                }
                .item__title:hover {
                    text-decoration: underline;
                    color: #2255AA;
                }
                .item__subtitle {
                    color: #5e9516;
                    font-size: 1.1rem;
                    font-family: 'PT Sans', sans-serif;
                }
                .item__location {
                    color: #494949;
                    font-size: 1.1rem;
                    font-family: 'Satisfy', cursive;
                }
                margin-bottom: 0.3rem;
            }

        }
    }


    // experience 
    .layout--experience {
        width: 60%;
        max-width: 60%;
        margin-left: auto;
        margin-right: auto;
    }

    .image--experience {
        margin-right: map-get($spacers, 5);
        border-radius: 12%;
        width: map-get($image, width-home-width);
        height: map-get($image, width-home-height);
    }
}


